        </el-form>
      </div>
    </el-card> -->

    <!-- 数据列表 -->
    <!-- <el-card class="operate-container"
             shadow="never">
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>

    </el-card> -->
    <div class="table-container">
      <el-table :data="tableList"
                style="width: 100%"
                ref="multipleTable">
        <el-table-column type="index"
                         width="50">
        </el-table-column>
        <el-table-column label="分销员昵称"
                         align="center">
          <template slot-scope="scope">{{scope.row.channelUsernickName}}</template>
        </el-table-column>
        <el-table-column label="分销员手机号"
                         prop="channelUserPhone"
                         align="center">
        </el-table-column>
        <el-table-column label="邀请方昵称"
                         align="center">
          <template slot-scope="scope">
            {{ scope.row.inviternickName == '' ? '--' : scope.row.inviternickName }}
          </template>
        </el-table-column>
        <el-table-column label="邀请方手机号"
                         align="center">
          <template slot-scope="scope">
            {{ scope.row.inviterPhone == '' ? '--' : scope.row.inviterPhone }}
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 固定在下面 -->
    <div class="fixedB">
      <el-button @click="goBack()"
                 type="primary">
        返回上一页
      </el-button>
      <!-- 页码 -->
      <el-pagination background
                     style="margin-top: 10px;margin-left:20px"
                     @size-change="handleSizeChange"
                     @current-change="pageChange"
                     layout="total,sizes,prev,pager,next,jumper"
                     :page-size="page_size"
                     :page-sizes="[10, 20, 30]"
                     :current-page="pageNum"
                     :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import {
  findChannelUserByGroupId,   //查看分组下的分销员
} from '@/api/distributionManagement/distributor'
// import { channelUserDetail,setUserBelong } from '@/api/distributionManagement/distributor'
export default {
  data() {
    return {
      list: {
        time: '',
      },
      id: this.$route.query.id,
      name: this.$route.query.name,
      tableList: [],
      pageNum: 1,
      page_size: 15,
      total: 0,
    }
  },
  created() {
    this.searchList()
  },
  mounted() {

  },
  methods: {
    //查询数据列表
    searchList() {
      findChannelUserByGroupId({
        shopId: this.shop.shopId,
        groupId: this.id,
        pageNum: this.pageNum,
        pageSize: this.page_size,
      }).then(res => {
        if (res.code == 200) {
          this.tableList = res.data.list
          this.total = res.data.total
        } else {
          this.$notify.error({
            title: '失败',
            message: res.message
          })
        }

      })
    },

    untying(row) {
      this.$confirm('是否解绑该用户？解绑后，该用户再购买将不会给该分销员以及上级分销员返佣。', '用户解绑', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        setUserBelong({
          id: row.id,
          isDelete: '1'
        }).then(res => {
          if (res.code == 200) {
            this.$message({
              message: '解绑成功',
              type: 'success'
            });
          } else {
            this.$message.error(res.message);
          }
        })
      }).catch(() => { });
    },

    //返回
    goBack() {
      this.$router.push({
        path: '/distributionManagement/distributor',
        query: {
          activeName: 'four'
        }
      })
    },

    //修改每页页数
    handleSizeChange(page) {
      this.page_size = page
      this.searchList()
    },
    //点击跳转页码
    pageChange(page) {
      this.pageNum = page
      this.searchList()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="scss" scoped>
.app-container {
  padding: 20px 20px 20px 20px;
  .main-title {
    display: flex;
    justify-content: space-around;
    color: #f59a23;
    // span{
    //   flex:1
    // }
  }
}
.fixedB {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 10px;
}
</style>